@font-face {
    font-family: "displayCard";
    src: url("../Fonts/DK\ Sugary\ Pancake.otf")
}

.viewContentDiv {
    width: fit-content;
}

.viewContentImage {
    margin-top: 1vh;
    margin-bottom: 1vh;

    max-width: 90vw;
    max-height: 70vh;

    border: solid;
    border-color: rgb(228,160,183);
    border-width: 5px;
}

.viewContentTitle {
    display: flex;
    color: white;
    font-size: 40px;
    font-family: "displayCard";

    padding: 4px;

    background-color: rgb(228,160,183);
    width: 100%;
    height: 50px;

    justify-content:space-between;
}

.viewContentDesc {
    color: white;
    font-size: 20px;
    font-family: "displayCard";

    padding: 4px;

    background-color: rgb(228,160,183);
    width: 100%;
}

.viewContentDelete {
    height: 90%;
    width:auto;
}

.viewContentDeleteImage {
    height: 100%;
}